import { get, uniq, filter } from 'lodash';
import {
  SEARCH_INITIATED,
  SEARCH_RESULTS_RECEIVED,
} from '../architecture/constants';

export const getInitialState = () => ({ searchResults: [], isLoading: false });

export default function(state = getInitialState(), action) {
  switch (action.type) {
    case SEARCH_INITIATED:
      return {
        ...state,
        searchResults: [],
        isLoading: true,
      };
    case SEARCH_RESULTS_RECEIVED:
      const searchResults = get(action, 'results.answers');
      const filenames = uniq(searchResults.map((result) => result.file_name));
      const colatedResults = filenames.map((filename) => {
        const fileResults = filter(
          searchResults,
          (result) => result.file_name === filename,
        );
        return {
          filename: fileResults[0].file_name,
          folderPath: fileResults[0].folder_path,
          mimeType: fileResults[0].mime_type,
          results: fileResults,
        };
      });
      return {
        ...state,
        searchResults: colatedResults,
        isLoading: false,
      };

    default:
      return state;
  }
}
