import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { withRouter } from 'react-router-dom';
import { get } from 'lodash';
import actionCreators from '../../actions';
import Dashboard from './Component';

const mapStateToProps = (state) => ({
  searchLoading: state.results.isLoading,
  searchResults: state.results.searchResults,
});

const mapDispatchToProps = (dispatch) => ({
  actions: {
    search: bindActionCreators(actionCreators.search, dispatch).search,
  },
});

class DashboardContainer extends Component {
  componentDidMount() {
    const searchTerm = get(this.props, 'match.params.term', '');
    if (searchTerm) {
      this.executeSearch(searchTerm);
    }
  }

  componentDidUpdate(prevProps) {
    const oldSearchTerm = get(prevProps, 'match.params.term', '');
    const newSearchTerm = get(this.props, 'match.params.term', '');
    if (oldSearchTerm !== newSearchTerm && !!newSearchTerm) {
      this.executeSearch(newSearchTerm);
    }
  }

  executeSearch(searchTerm) {
    const {
      actions: { search },
    } = this.props;

    search(searchTerm);
  }

  render() {
    const {
      match: {
        params: { action, term },
      },
      searchLoading,
      searchResults,
    } = this.props;
    const searchActive = action === 'search' && !!term;
    return (
      <Dashboard
        searchActive={searchActive}
        searchLoading={searchLoading}
        searchResults={searchResults}
      />
    );
  }
}

DashboardContainer.propTypes = {
  match: PropTypes.shape({
    params: PropTypes.shape({
      action: PropTypes.string,
      term: PropTypes.string,
    }).isRequired,
  }).isRequired,
  searchLoading: PropTypes.bool.isRequired,
  searchResults: PropTypes.array, // TODO: Define better contract
  actions: PropTypes.shape({
    search: PropTypes.func.isRequired,
  }).isRequired,
};

DashboardContainer.defaultProps = {
  searchResults: [],
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(DashboardContainer));
