import {
  SEARCH_INITIATED,
  SEARCH_RESULTS_RECEIVED,
} from '../architecture/constants';
import searchService from '../services/search';

const resultsReceived = (response) => ({
  type: SEARCH_RESULTS_RECEIVED,
  results: response,
});

const initiateSearch = () => ({
  type: SEARCH_INITIATED,
});

export function search(searchTerm) {
  return (dispatch) => {
    dispatch(initiateSearch());
    searchService
      .search(searchTerm)
      .then((response) => {
        dispatch(resultsReceived(response));
      })
      .catch((error) => {
        throw error;
      });
  };
}
