import React from 'react';
import PropTypes from 'prop-types';
import injectSheet from 'react-jss';
import searchIcon from '../../assets/network.svg';

const styles = {
  container: {
    display: 'flex',
    alignItems: 'center',
  },
  icon: {
    width: 48,
    height: 48,
    margin: [0, 8],
  },
  search: {
    width: '100%',
    height: 32,
    borderRadius: 3,
    color: '#666666',
    fontSize: 14,
    padding: [0, 10],
    border: { style: 'solid', width: 1, color: '#CCC' },
  },
  submit: {
    height: 32,
    width: 75,
    margin: [0, 5],
    borderRadius: 3,
    border: { style: 'solid', width: 1, color: 'transparent' },
    fontSize: 14,
    color: '#666',
  },
};

const Search = ({
  classes,
  term,
  changeHandler,
  searchHandler,
  keyPressHandler,
}) => (
  <div className={classes.container}>
    <img className={classes.icon} src={searchIcon} alt="network" />
    <input
      className={classes.search}
      type="text"
      value={term}
      onChange={changeHandler}
      onKeyPress={keyPressHandler}
    />

    <button className={classes.submit} type="button" onClick={searchHandler}>
      Search
    </button>
  </div>
);

Search.propTypes = {
  term: PropTypes.string,
  changeHandler: PropTypes.func.isRequired,
  searchHandler: PropTypes.func.isRequired,
  keyPressHandler: PropTypes.func.isRequired,
};

Search.defaultProps = {
  term: '',
};

export default injectSheet(styles)(Search);
