import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { get } from 'lodash';
import actionCreators from '../../actions';
import Search from './Component';

const mapDispatchToProps = (dispatch) => ({
  actions: {
    search: bindActionCreators(actionCreators.search, dispatch).search,
  },
});

class SearchContainer extends Component {
  state = {
    searchTerm: '',
  };

  changeHandler = (e) => {
    this.setState({
      searchTerm: e.target.value,
    });
  };

  searchHandler = () => {
    const { history, actions } = this.props;
    const { searchTerm } = this.state;
    history.push(`/search/${searchTerm}`);
    actions.search(searchTerm);
  };

  componentDidMount() {
    const searchTerm = get(this.props, 'match.params.term', '');
    this.setState({ searchTerm });
  }

  componentDidUpdate(prevProps) {
    const oldSearchTerm = get(prevProps, 'match.params.term', '');
    const newSearchTerm = get(this.props, 'match.params.term', '');
    if (oldSearchTerm !== newSearchTerm) {
      this.updateSearchTerm(newSearchTerm);
    }
  }

  updateSearchTerm(searchTerm) {
    this.setState({
      searchTerm,
    });
  }

  keyPressHandler = (e) => {
    if (e.key === 'Enter') {
      this.searchHandler();
    }
  };

  render() {
    const { searchTerm } = this.state;
    return (
      <Search
        term={searchTerm}
        changeHandler={this.changeHandler}
        searchHandler={this.searchHandler}
        keyPressHandler={this.keyPressHandler}
      />
    );
  }
}

SearchContainer.propTypes = {
  actions: PropTypes.shape({
    search: PropTypes.func.isRequired,
  }).isRequired,
};

export default connect(
  null,
  mapDispatchToProps,
)(withRouter(SearchContainer));
