import React from 'react';
import PropTypes from 'prop-types';
import injectSheet from 'react-jss';
import { Scrollbars } from 'react-custom-scrollbars';
import background from '../../assets/background-2.jpg';
import loaderImage from '../../assets/ajax-loader.gif';
import Search from '../Search';
import SearchResult from '../SearchResult';

const styles = {
  container: {
    height: '100%',
  },
  header: {
    height: 60,
    backgroundColor: '#353A3F',
    borderBottom: { style: 'solid', width: 1, color: '#333333' },
    position: 'absolute',
    top: 0,
    width: '100%',
  },
  content: {
    height: '100%',
    borderTop: { style: 'solid', width: 61, color: 'white' },
    borderBottom: { style: 'solid', width: 51, color: 'white' },
    backgroundColor: 'white',
    boxSizing: 'border-box',
    backgroundImage: `url('${background}')`,
    backgroundSize: 'cover',
  },
  footer: {
    height: 50,
    backgroundColor: '#F7FBFE',
    borderTop: { style: 'solid', width: 1, color: '#999' },
    position: 'absolute',
    bottom: 0,
    width: '100%',
    color: '#666',
    fontFamily: 'Verdana',
    lineHeight: 50,
    fontSize: 12,
    paddingLeft: 50,
    boxSizing: 'border-box',
  },
  searchTextboxContainer: {
    position: 'absolute',
    transition: '0.5s',
    transitionTimingFunction: 'ease-in-out',
    zIndex: 100,
  },
  searchOverlay: {
    width: '100%',
    height: '100%',
    backgroundColor: 'rgba(255, 255, 255, 0.8)',
    transition: '0.5s',
    display: 'flex',
  },
  resultsPane: {
    width: '50%',
    height: '100%',
  },
  previewPane: {
    width: '50%',
    height: '100%',
  },
  loadingIndicatorContainer: {
    display: 'flex',
  },
  loadingIndicator: {
    width: 50,
    height: 50,
    margin: [50, 'auto', 0, 'auto'],
  },
};

const Dashboard = ({ classes, searchActive, searchLoading, searchResults }) => {
  const searchBoxStyles = {
    top: searchActive ? 5 : 320,
    left: searchActive ? '0%' : '25%',
    width: '50%',
  };
  const searchOverlayStyles = {
    opacity: searchActive ? 1 : 0,
    visibility: searchActive ? 'visible' : 'hidden',
  };
  return (
    <div className={classes.container}>
      <div className={classes.header} />
      <div className={classes.content}>
        <div className={classes.searchTextboxContainer} style={searchBoxStyles}>
          <Search />
        </div>
        <div className={classes.searchOverlay} style={searchOverlayStyles}>
          <div className={classes.resultsPane}>
            {searchLoading && (
              <div className={classes.loadingIndicatorContainer}>
                <img
                  src={loaderImage}
                  className={classes.loadingIndicator}
                  alt="loading"
                />
              </div>
            )}
            {!searchLoading && !!searchResults.length && (
              <Scrollbars>
                {searchResults.map((result) => (
                  <SearchResult
                    key={`search-result-${result.filename}`}
                    result={result}
                  />
                ))}
              </Scrollbars>
            )}
          </div>

          <div className={classes.previewPane}>&nbsp;</div>
        </div>
      </div>
      <div className={classes.footer}>© 2018 Noam by Peruse.io</div>
    </div>
  );
};

Dashboard.propTypes = {
  searchActive: PropTypes.bool.isRequired,
  searchLoading: PropTypes.bool.isRequired,
  searchResults: PropTypes.array.isRequired, // TODO: Define better contract
};

export default injectSheet(styles)(Dashboard);
