import React from 'react';
import PropTypes from 'prop-types';
import injectSheet from 'react-jss';
import { flatten } from 'lodash';

const styles = {
  resultContainer: {
    margin: 10,
    padding: 20,
    backgroundColor: '#FFFFFF',
    borderRadius: 5,
    cursor: 'default',
    border: 'solid 1px white',
    transition: '0.3s',
    '&:hover': {
      border: 'solid 1px rgb(65, 136, 222)',
    },
  },
  titleLine: {
    display: 'flex',
  },
  filename: {
    fontSize: 20,
    color: '#666666',
  },
  mimeType: {
    color: 'white',
    backgroundColor: '#DD0000',
    padding: [2, 5],
    borderRadius: 10,
    textTransform: 'uppercase',
    fontWeight: 'bold',
    fontSize: 12,
    height: 15,
    margin: [3, 0, 0, 10],
  },
  filePath: {
    fontSize: 16,
    color: '#0099FF',
    padding: [7, 0],
  },
  resultsPane: {
    margin: [0, -10],
  },
  match: {
    padding: [10],
    fontSize: 14,
    color: '#999999',
    transition: '0.3s',
    '&:hover': {
      backgroundColor: 'rgba(150, 200, 255, 0.1)',
      borderRadius: 5,
    },
  },
  resultActions: {
    display: 'flex',
    justifyContent: 'flex-end',
    marginTop: 10,
  },
  button: {
    padding: [7, 15],
    border: 'none',
    borderRadius: 3,
    fontSize: 14,
    color: 'white',
    marginLeft: 5,
    cursor: 'pointer',
  },
  previewButton: {
    backgroundColor: 'rgb(241, 156, 56)',
  },
  downloadButton: {
    backgroundColor: 'rgb(65, 136, 222)',
  },
};

const SearchResult = ({ classes, result }) => {
  const filename = result.filename.split('.')[0];
  const filePath = flatten([result.folderPath, result.filename]);
  return (
    <div className={classes.resultContainer}>
      <div className={classes.titleLine}>
        <div className={classes.filename}>{filename}</div>
        <div className={classes.mimeType}>
          {result.mimeType.replace('application/', '')}
        </div>
      </div>
      <div className={classes.filePath}>{filePath.join(' » ')}</div>
      <div className={classes.resultsPane}>
        {result.results.map((match, index) => (
          <div
            key={`match-${index}`} // eslint-disable-line react/no-array-index-key
            className={classes.match}
            dangerouslySetInnerHTML={{ __html: match.paragraph }} // eslint-disable-line react/no-danger
          />
        ))}
        <div className={classes.resultActions}>
          <button
            type="button"
            className={`${classes.button} ${classes.previewButton}`}
          >
            Preview
          </button>
          <button
            type="button"
            className={`${classes.button} ${classes.downloadButton}`}
          >
            Download
          </button>
        </div>
      </div>
    </div>
  );
};

SearchResult.propTypes = {
  result: PropTypes.object.isRequired,
};

SearchResult.defaultProps = {};

export default injectSheet(styles)(SearchResult);
